<template>
  <div>
    <div class="top">
      <h4>健康告知</h4>
      <div
          v-for="item in productData.notifyList"
          :key="item.sortNum"
          class="notice-content"
        >
          {{ item.notifyDesc }}
          
        </div>
      <div class="but">
        <div @click="partYes">部分为是</div>
        <div @click="allNo">以上均为否</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},

  data() {
    return {
      productData:''
    };
  },

  created() {},
  mounted() {
    this.productData = JSON.parse(sessionStorage.getItem("productData")) 
    // console.log(this.productData,'健康告知的=');
    // // console.log('上个页面传过来的数据',JSON.parse(this.$route.query.KDPC) )
    // console.log(this.$route.query);
  },
  methods: {
    //   以上均为否
    allNo() {
      this.$router.push({
        path: "/informationEntry",
        query: {
          radio: this.$route.query.radio,
        },
      });
    },
    // 部分为是
    partYes() {
      this.$router.push({
        path: "/insuranceInformation",
      });
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='less' scoped>
.top {
  .notice-content {
  font-size: 12px;
  text-align: left;
  line-height: 1.8em;
  margin-top: 10px;
  }
  padding-bottom: 50px;
  margin: 10px;
  p {
    margin: 10px;
  }
}
.but {
  display: flex;
  position: fixed;
  bottom: 1%;
  width: 95%;
  height: 6%;
  div {
    flex: 1;
  }
  div:nth-child(1) {
    border: 1px solid rgba(0, 0, 0, 0.34);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 18px;
    color: #000;
    margin-right: 10px;
  }
  div:nth-child(2) {
    border: 1px solid rgba(0, 0, 0, 0.34);
    border-radius: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background: #1989fa;
  }
}
</style>
